import {
  safeAdd
} from './number'

const SECOND = 1000
const MINUTE = SECOND * 60
const HOUR = MINUTE * 60
const DATE = HOUR * 24

// 在safari手机浏览器new Date('2018-01-01')返回'Invalid Date'
export function dateParse(date) {
  return new Date(typeof date === 'string' ? date.replace(/-/g, '/') : date)
}

export function dateFormat(date, fmt = 'yyyy-MM-dd hh:mm:ss') {
  const o = {
    'M+': date.getMonth() + 1,
    'd+': date.getDate(),
    'h+': date.getHours(),
    'm+': date.getMinutes(),
    's+': date.getSeconds(),
    'q+': Math.floor((date.getMonth() + 3) / 3),
    'S': date.getMilliseconds()
  }
  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
  }
  for (let k in o) {
    if (new RegExp('(' + k + ')').test(fmt)) {
      fmt = fmt.replace(RegExp.$1, RegExp.$1.length === 1 ? o[k] : ('00' + o[k]).substr(('' + o[k]).length))
    }
  }
  return fmt
}

/**
 * 返回给定年月有多少天
 * @param {number} month 传1~12
 * @return {number}
 */
export function getNumberOfDaysInTheMonth(year, month) {
  return new Date(year, month, 0).getDate()
}

// 处于同一天返回0，每往前一天增1
export function getDateDiff(begin, end) {
  begin = new Date(begin.getFullYear(), begin.getMonth(), begin.getDate())
  return ~~((end - begin) / DATE)
}

/**
 ************** 以下方法与业务相关 *****************
 */
export function timeDiffFormat(date) {
  if(!date) {
    return ''
  }
  const now = new Date()
  const dateDiff = getDateDiff(date, now)
  if(dateDiff === 0) {
    const diff = now - date
    if(diff >= 1 * HOUR) {
      return ~~(diff / HOUR) + '小时前'
    } else {
      if(diff >= 1 * MINUTE) {
        return ~~(diff / MINUTE) + '分钟前'
      } else {
        return '刚刚'
      }
    }
  } else if(dateDiff === 1) {
    return '昨天'
  } else {
    return dateFormat(date, 'yyyy-MM-dd')
  }
}

/**
 * 用来格式化倒计时
 * 使用{}括住表达单位符表示变量
 * 最大单位的对应值为该值的总数，其余为余数
 * @param {number} s
 * @param {string} fmt
 * @param {function(val) => newVal|[string, number]|string} fix 最小单位的对应值可以为小数，通过修改默认行为
 * 使用[]括住表示当表达式里面的单位的总数为0时不显示该段文本，请保证[]操作符从最大单位开始使用
 * (9, '{s}秒') => '9秒'
 * (9, '{ss}秒') => '09秒'
 * (9.9, '{s}秒') => '9.9秒'
 * (9.9, '{s+}秒') => '10秒'
 * (9.9, '{s-}秒') => '9秒'
 * (9.5, '{s*}秒') => '10秒'
 * (9.4, '{s*}秒') => '9秒'
 * (9.95, '{s*1}秒') => '10秒'
 * (9.94, '{s*1}秒') => '9.9秒'
 * (9, '{mm}分{ss}秒') => '00分09秒'
 * (9, '[{mm}分]{ss}秒') => '09秒'
 * (99, '[{mm}分]{ss}秒') => '01分09秒'
 * (9999, '[{mm}分]{ss}秒') => '01分09秒'
 */
export function countdownFormat(s = 0, fmt, fix) {
  // get fix
  if(fix && typeof fix !== 'function') {
    const [methodName, len = 0] = Array.isArray(fix) ? fix : [fix]
    const e = Math.pow(10, len)
    fix = val => Math[methodName](val * e) / e
  }

  const ORDER = 'smhd'
  let pattern = ''
  for(let i = 0, flag = false; i < ORDER.length; i++) {
    const unit = ORDER[i]
    if(new RegExp(`{${unit}+}`).test(fmt)) {
      pattern += unit
      flag = true
    } else if(flag) {
      break
    }
  }
  // get map
  const map = convertSecond(s, pattern, fix)

  let enableClear = true
  for(let i = pattern.length - 1; i > -1; i--) {
    const unit = pattern[i]
    const isOptional = new RegExp(`\\[([^\\[]*{${unit}+}[^\\]]*)]`).exec(fmt)
    if(isOptional) {
      const [str, str2] = isOptional
      if(enableClear && map[unit] === 0) {
        fmt = fmt.replace(str, '')
        continue
      } else {
        fmt = fmt.replace(str, str2)
        enableClear = false
      }
    }
    const [str, actualUnit] = new RegExp(`{(${unit}+)}`).exec(fmt)
    let val = map[unit] + ''
    while(val.length < actualUnit.length) {
      val = '0' + val
    }
    fmt = fmt.replace(str, val)
  }
  return fmt
}

export function second2MinuteSecond(s) {
  return countdownFormat(s, '{mm}:{ss}', 'floor')
}

/**
 * @param {number} s
 * @param {string} pattern e.g. 'mh'
 */
export function convertSecond(s, pattern, fix) {
  const ORDER = 'smhd'
  const RULES = [60, 60, 24]
  const map = {}
  const values = [s]
  const offset = ORDER.indexOf(pattern)
  let i = 0
  for(; i < offset; i++) {
    values[i + 1] = values[i] / RULES[i]
  }
  // 此时的values[i]做为后面计算的基数
  if(fix) {
    values[i] = fix(values[i])
  }

  for(; i < offset + pattern.length - 1; i++) {
    values[i + 1] = ~~(values[i] / RULES[i])
    values[i] = safeAdd(values[i], -values[i + 1] * RULES[i])
  }
  for(let i = offset; i < offset + pattern.length; i++) {
    map[ORDER[i]] = values[i]
  }
  return map
}
